const aboutBlockLinks = document.querySelectorAll('.about-webstoryz__link');
let aboutBlockActiveImage = document.getElementById('popularFormatImage');
let aboutBlockActiveLink = document.getElementById('popularFormat');

function aboutBlockImagesChange(event) {
  if (!event.target.classList.contains('active') && (event.target.classList.contains('about-webstoryz__link'))) {
    event.target.classList.add('active');
    aboutBlockActiveLink.classList.remove('active');
    aboutBlockActiveLink = event.target;
    aboutBlockActiveImage.classList.remove('active');
    aboutBlockActiveImage = document.getElementById(`${event.target.id}Image`);
    aboutBlockActiveImage.classList.add('active');
  }
}

aboutBlockLinks.forEach((link) => {
  link.addEventListener('click', aboutBlockImagesChange);
})
